* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

body {
  overflow: overlay;
}


  ::-webkit-scrollbar-track {
    -webkit-box-shadow: none !important;
    background-color: transparent;
  }
  ::-webkit-scrollbar {
    width: 8px !important;
    background-color: transparent;
  }
  ::-webkit-scrollbar-thumb {
    background-color: rgba(250, 250, 58, 0.712);
    border-radius: 8px;
  }

@media only screen and (max-width: 900px) {
  /* Navbar & Logo */
  /* Logo */
  .const-h1 h1 {
    font-size: 28px;
  }
  .container-logo ul li a:hover {
    animation: none !important;
  }
  .const-h1 h1 div {
    display: none;
  }
  /* Navbar */
  .container-navegation {
    display: none;
  }
  /* Section Home */
  section.home {
    min-height: 642px;
  }
  .container-video, .video {
    min-height: 692px;
  }
  .max-width.home {
    height: 100vh;
    display: flex;
    justify-content:left ;
  }
  .container-home {
    padding: 0 !important;
    margin: 0px 25px;
  }
  .container-home div{
    font-size: 20px;
  }
  .container-home .text-3 h1 p, .container-home .text-3 h1 a span{
    font-size: 26px;
  }
  .container-home .text-3 h1 a {
    height: 28px;
  }
  .container-button .here-button {
    font-size: 32px;
  }
  /* Section About */
  .max-width.about {
    padding: 0 !important;
  }
  .about-container {
    padding: 25px 5px 0 5px!important;
  }
  .about-container h4{
    font-size: 28px !important;
    margin-top: 0 !important;
  }
  .about-container .vl-line {
    height: 30px;
    padding-bottom: 30px;
    margin-bottom: 8px;
  }
  .about-text p {
    font-size: 19px !important;
  }
  /* Section Skills */
  .max-width.skills {
    padding: 0;
  }
  .title-skills {
    font-size: 20px !important;
    height: auto !important;
    padding: 10px 0 !important;
  }
  .wrapper-icons .icon {
    font-size: 50px !important;
    margin-top: 20px !important;
    transition: .8s ease-in-out !important;
  }
  .icon svg:hover {
    animation: bounce 1s, letterup 1s !important;
  }
  /* Section Project's */
  section.projects {
    min-height: 642px;
  }
  .max-width.projects {
    padding: 0;
  }
  .title-projects h1 {
    font-size: 42px !important;
    padding: 0 5px;
  }
  .build-img {
    width: 70% !important;
  }
  /* Section Contact */

  section.contact {
    min-height: 642px;
  }
  .max-width.contact {
    padding: 0;
  }
  /* Inputs contact */
  .title-contact h1, .title-contact{
    font-size: 32px !important;
    height: auto !important;
  }
  .contact .wrap {
    margin-top: 20px;
    width: 93.4%;
    padding-left: 24px;
  }
  .wrap .container-inputs {
    width: 100%;
    padding-top: 25px;
  }
  .container-inputs .input-name {
    margin-bottom: 24px;
  }
  .container-inputs .input-name, .container-inputs .input-email {
    width: 100%;
  }
  .wrap .button button {
    width: 100%;
  }
  /* Social media contact */
  .wrap .container-social-media {
    width: 100%;
    height: auto;
    font-size: 35px;
  }
  .wrap .container-social-media .social-media {
    flex-direction: row;
    justify-content: space-evenly;
  }
  .social-media a {
    padding: 10px 0 !important;
  }
  .icon-gh svg {
    color: #211F1F;
  }

  .icon-tw svg {
      color: #1DA1F2;
  }

  .icon-lk svg {
      color: #0e76a8;
  }

  .icon-ig svg {
      color: #fbad50;
  }
  .social-media a svg:hover {
    animation: iconsup 1s !important;
  }
  /* Footer */
  footer .span-footer{
    font-size: 10px;
    letter-spacing: 1px;
    height: 25px;
  }
  .span-footer p {
    height: 25px !important;
  }
}

@media only screen and (max-width: 1324px) {
  .wrap .container-inputs .input-name, 
  .wrap .container-inputs .input-email {
    width: 100%;
  }
  .input-name {
    margin-bottom: 24px;
  };
}
.container-logo {
    display: flex;
    align-items: center;
    position: fixed;
    top: -7px;
    left: 13px;
    width: 23%;
}

.container-logo ul, .const-h1 ul,.const-h1 li,.const-h1 a,.const-h1 {
    list-style: none;
    width: 100%;
}

.const-h1 h1 .diferent-color {
    color: rgb(255, 244, 96);
}

.const-h1 div span span {
    color: rgb(255, 244, 96);
}

.container-logo ul li a {
    color: rgb(170, 168, 168);
    font-size: 23px;
    font-family: 'Gloria Hallelujah', cursive;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    text-decoration: none;
    letter-spacing: 5px;
    display: flex;
}

.container-logo ul li a:hover, .const-h1 h1 div .first-span{
    animation: color-infinite infinite 18s .4s;
}

.const-h1 div{
    position:fixed;
    top: 22px;
    font-size: 26px;
    opacity: 1;
    width: 20%;
    opacity: 0;
    padding-left: 92px;
    transition: .8s ease-in-out;
}


.const-h1 h1 div:hover {
    opacity: 1;
}

@keyframes color-infinite {
    0% {
        color: rgb(170, 168, 168);
    }
    5% {
        color: rgb(255, 244, 96);
    }
    10% {
        color: rgb(163, 162, 162);
    }
    15% {
        color: rgb(241, 232, 94);
    }
    20% {
        color: rgb(145, 145, 145);
    }
    25% {
        color: rgb(216, 207, 79);
    }
    30% {
        color: rgb(129, 128, 128);
    }
    35% {
        color: rgb(185, 178, 68);
    }
    40% {
        color: rgb(117, 117, 117);
    }
    45% {
        color: rgb(172, 164, 64);
    }
    50% {
        color: rgb(105, 104, 104);
    }
    55% {
        color: rgb(163, 156, 56);
    }
    60% {
        color: rgb(95, 95, 95);
    }
    65% {
        color: rgb(146, 140, 52);
    }
    70% {
        color: rgb(82, 82, 82);
    }
    75% {
        color: rgb(124, 119, 45);
    }
    80% {
        color: rgb(77, 77, 77);
    }
    85% {
        color: rgb(109, 103, 41);
    }
    90% {
        color: rgb(66, 66, 66);
    }
    95% {
        color: rgb(97, 93, 37);
    }
    100% {
        color: rgb(56, 56, 56);
    }
}
.container-navegation {
    position: fixed;
    top: 20%;
    right: 25px;
    max-width: 900px;
    text-align: right;
}

.container-navegation ul li {
    margin: 30px 0;
    list-style: none;
}


.container-navegation ul li a svg{
    color: rgb(134, 134, 134);
    font-size: 53px;
    cursor: pointer;
}

.container-navegation ul li a svg:hover {
    animation: float 2s ease-in-out infinite;
}

@keyframes float {
	0% {
		transform: translatey(0px);
	}
	50% {
		transform: translatey(-17px);
	}
	100% {
		transform: translatey(0px);
	}
}

