.wrapper-icons .icon{
    margin-top: 50px;
    display: inline-block;
    padding: 0 12px;
    font-size: 60px;
}  

/* Icons colors */

.icon.html svg {
    color: #e34f26;
}

.icon.css svg {
    color: #2965f1;
}

.icon.js svg {
    color: #323330;
    border-radius: 8px;
    background: #ffe600;
}

.icon.sass svg {
    color: #c69;
}

.icon.react svg {
    color: #61DBFB;
}

.icon.ts svg {
    color: #007acc;
    border-radius: 8px;
    background: #fff;
}

.icon.git svg {
    color: #FFFFFF;
}
/* Icons hover */

.icon svg:hover {
    animation: bounce 2s, letterup .5s forwards;
}



@keyframes bounce {
    0%,
    25%,
    50%,
    75%,
    100% {
      transform: translateY(0);
    }
    40% {
      transform: translateY(-20px);
    }
    60% {
      transform: translateY(-12px);
    }
  }

@keyframes letterup {
    from {
        font-size: 60px;
    }
    to {
        font-size: 70px;
    }
}
